
.App {
  text-align: center;
}

div {
  box-sizing: border-box;

}
.list{
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid #F9E1F6;
  width: 100%;
  
}

.modal {
  margin-top: 20px;
  padding: 20px;
  background: #eee;
  text-align: left;
  

}

.black-nav {
  display: flex;
  background : #dda0dd;
  color: #800080;
  text-align: center;
  justify-content: space-between;
  height: 60px;
  padding: 0 20px;
  position: relative;
}

.left-text {
  color: black;
  font-size : 14px;
  margin:0;
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
}

.center-text {
  color: #800080;
  font-size: 18px;
  margin: 0; /* 기본 마진 제거 */
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%); /* 수직 및 수평 중앙 정렬 */
  left: 50%;
  
}



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
